
@import 'settings-panel.css';

@import url("http://fonts.googleapis.com/css?family=Open%20Sans+Condensed:400,700");
@import url("http://fonts.googleapis.com/css?family=Open+Sans:400italic,400,700");
@import url("http://fonts.googleapis.com/css?family=Volkhov:400italic");

body {
	font-family: Open Sans,'Helvetica Neue',Arial,sans-serif;
	background: #FFF url("../images/pattern/pattern11.png") fixed; 
	overflow-x: hidden;
	overflow-y: scroll;
}

a {
	color: #8dc73f;
	transition: .4s;
}

a, a:hover, a:focus, a:active {
	outline: 0;
	text-decoration: none;
	transition: .4s;
	color:inherit;
}

a:hover {
	color: #8dc73f;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	font-family: 'Open Sans','Helvetica Neue',Arial,sans-serif;
	color:#000;
	text-transform: uppercase;
}

/*h2{
	font-size: 18px;
	letter-spacing: 2px;
}

h4{
	font-size: 14px;
	letter-spacing: 4px;
}*/

p {
	margin-bottom: 17px;
	font-size: 13px;
	line-height: 23px;
	color: #878787;
}

ul, li {
	list-style: none;
	padding: 0px;
	margin: 0px;
}
ul:focus,
li:focus {
	outline: none;
}
.no-padding {
	padding: 0 !important;
}
.padding-top-70 {
	padding: 70px 0 0 0;
}

@media only screen and (max-width : 767px) {
	.margin-bottom-xs-30 {
		margin-bottom: 30px;
	}
}
.outer-wrapper {
	width: 100%;
	position: relative;
	overflow: hidden;
	background:#fff;
}

.outer-wrapper.boxed {
	width: 98%;
	max-width:1250px;
	margin-top: 20px;
	margin-left: auto;
	margin-right: auto;
	box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.06);
}

.outer-wrapper.boxed header {
	width: 100%;
	max-width:1250px;
	margin-left: auto;
	margin-right: auto;
}


#header-main.affix a.navbar-brand {
	font-size: 31px !important;
	margin: 5px 0 0;
}

#header-main.affix {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	margin-top: 0;
	padding: 10px 0;
	min-height: inherit;
	z-index: 999;
	background: #ffffff;
	background: rgba(255,255,255,1);
	border-bottom: 1px solid #efefef;
}

#header-main.affix .navbar {
	margin-top: 0px;
}

#header-main.affix .navbar-nav > li > .dropdown-menu {
	margin-top: 12px !important;
}

#header-main.affix .navbar-brand img {
	width: 80%;
	margin-top: 7px;
}

#header-main.affix .navbar-brand {
	line-height: 38px;
}

