	content: "\f105";
	font-size: 13px;
	padding-right: 5px;
}

#header-main .yamm-fw .dropdown-menu li {
	list-style: none;
	padding: 0;
}

#header-main .yamm-fw .nav.navbar-right .dropdown-menu {
	left: auto;
