	left: 6%;
}

#about-section2 .content-tab .tab-cont[data-tab="2"]:before {
	left: 33%;
}

#about-section2 .content-tab .tab-cont[data-tab="3"]:before {
	left: 59%;
}

#about-section2 .content-tab .tab-cont[data-tab="4"]:before {
	left: 86%;
}

#about-section2 .content-tab:after {
	position: absolute;
