#carousel_five .owl-prev {
	background:none;
	/* border: 1px solid #000; */
	width: 44px;
	height: 44px;
	line-height: 42px;
	color: #fff;
	border-radius: 50%;
	font-size: 0px;
	opacity: 0;
	position: absolute;
	left: -50px;
		transition:.4s;
	top: 50%;
	margin-top: -22px;
	transition: .4s;
}





#carousel_five .owl-prev:after {
	content: "\f104";
	font-family: 'FontAwesome';
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0px;
	left: 0px;
	right: 0px;
	font-size: 25px;
	color: #000;
	line-height: 42px;
	transition: .4s;
}

#carousel_five .owl-next {
	background:none;
	/* border: 1px solid #000; */
	width: 44px;
	height: 44px;
	line-height: 42px;
	color: #fff;
	border-radius: 50%;
	font-size: 0px;
	opacity: 0;
	position: absolute;
	right: -50px;
		transition:.4s;
	top: 50%;
	margin-top: -22px;
	transition: .4s;
}

#carousel_five .owl-next:after {
	content: "\f105";
	font-family: 'FontAwesome';
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0px;
	left: 2px;
	right: 0px;
	font-size: 25px;
	color: #000;
	line-height: 42px;
	transition: .4s;
}

#carousel_five:hover .owl-prev  {
	opacity:0.5 !important;
		left:0px !important;
		transition:.4s !important;
}

#carousel_five:hover .owl-next  {
	opacity:0.5 !important;
		right:0px !important;
		transition:.4s !important;
}

#carousel_five .owl-prev:hover {
	background:none;
	border:none;
	width: 44px;
	height: 44px;
	line-height: 42px;
	color: #000;
	border-radius: 50%;
	font-size: 0px;
	opacity: 1;
	transition:.4s;
	position: absolute;
	left: 0;
	top: 50%;
	margin-top: -22px;
	transition: .4s;
}

#carousel_five .owl-next:hover {
	background: none;
	border: none;
	width: 44px;
	height: 44px;
	line-height: 42px;
	color: #000;
	border-radius: 50%;
	font-size: 0px;
	opacity: 1;
	position: absolute;
	right: 0px;
	transition:.4s;
	top: 50%;
	margin-top: -22px;
	transition: .4s;
}
#one-gym #carousel_five .owl-next:after {
	color: #D2C6C6;
}
#one-gym #carousel_five .owl-prev:after {
	color: #D2C6C6;
}


#carousel_five .owl-prev:hover:after ,
#carousel_five .owl-next:hover:after {
	color: #000;
	transition: .4s;
}

